import React, { Component } from "react";
import styled from "styled-components";
import GlobalStyles from "../../assets/styles/GlobalStyles";
import { Title } from "../../components/Title/Title";
import IconIMG from "../../components/IconIMG/IconIMG";
import contactGirl from "../../assets/images/contact-template-girl.jpg";
import girlInGrapes from "../../assets/images/girl-in-grapes.jpg";
import handsWithGrapes from "../../assets/images/hands-with-grapes.jpg";

const Contact = styled.section`
  min-height: 200vh;
  background-color: white;
  color: black;
  overflow: hidden;
  position: relative;
  background-color: #e5dede;
`;

const Header = styled.header`
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 20px;
  margin-top: 70px;

  .hands-grapes {
    width: 85%;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }

  h1 {
    position: static;
    width: 100%;
    align-self: flex-start;
    transform: translateY(-20%);
    font-size: 27px;
    span {
      position: relative;
      display: block;
      ::after {
        position: absolute;
        top: 55%;
        left: 10%;
        z-index: -1;
        display: block;
        content: "";
        background-color: #cd97c4;
        width: 80%;
        height: 2px;
      }
    }
  }
`;

class ContactTemplate extends Component {
  state = {};

  render() {
    return (
      <>
        <Contact>
          <GlobalStyles isScrollable={this.props.isScrollable} />
          <Header>
            <IconIMG
              className={"hands-grapes"}
              src={handsWithGrapes}
              alt={"dłonie z winogronami"}
            />
            <Title>
              najwyższa jakość usług
              <br /> w najdogodniejszy
              <span>sposób</span>
            </Title>
          </Header>
        </Contact>
      </>
    );
  }
}

export default ContactTemplate;

